import Noodl from "@noodl/noodl-sdk";
import { Calendar, dayjsLocalizer } from "react-big-calendar";
import "react-big-calendar/lib/addons/dragAndDrop/styles.css";

import dayjs from "dayjs";
import customParseFormat from "dayjs/plugin/customParseFormat";
dayjs.extend(customParseFormat);

import "react-big-calendar/lib/css/react-big-calendar.css";
import withDragAndDrop from "react-big-calendar/lib/addons/dragAndDrop";

const DnDCalendar = withDragAndDrop(Calendar);

const localizer = dayjsLocalizer(dayjs);

const Scheduler = (props) => {
  let events = [];
  if (props.events && props.dateFormat) {
    events = props.events.map((e) => ({
      start: dayjs(e.start, props.dateFormat).toDate(),
      end: dayjs(e.end, props.dateFormat).toDate(),
      title: e.title,
    }));
  }

  return (
    <div>
      <DnDCalendar
        localizer={localizer}
        events={events}
        startAccessor="start"
        endAccessor="end"
        style={{ height: 500 }}
      />
    </div>
  );
};

const SchedulerNode = Noodl.defineReactNode({
  name: "care.huber.scheduler",
  displayName: "Calendar",
  category: "Scheduler",
  initialize() {},
  getReactComponent() {
    return Scheduler;
  },
  inputs: {},
  inputProps: {
    dateFormat: {
      displayName: "Date Format",
      type: "string",
      default: "YYYY-MM-DDTHH:mm",
    },
    events: {
      displayName: "Events",
      type: "array",
    },
  },
  outputProps: {},
});
export default SchedulerNode;
